body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.result {
  background: #ffffff1a;
  padding: .1em .7em;
  padding-bottom: .6em;
  margin-bottom: 3.5rem;
}

.result em {
  background: #ff03;
}

.result .meta {
  float: right;
  color: gray;
  font-size: 0.9em;
}

.result h1 {
  font-size: 1.15em;
}

.result .source {
  font-size: 0.9em;
  color: gray;
}

.result .source a {
  text-decoration: underline;
  font-style: italic;
  color: inherit;
}

ul.filter {
  list-style-type: none;
  padding: 0;
  max-height: 250px;
  overflow-y: auto;
}

